.form-franchise {
  max-width: 240px;
  margin: 0 auto;

  @media screen and (min-width: $tablet-min) {
    max-width: 368px;
  }

  &__field {
    position: relative;
    margin-bottom: 16px;
  }

  &__input,
  &__textarea {
    width: 100%;
    padding: 11px 16px;
    background-color: rgba(248, 210, 78, 0.1);
    border: 1px solid $bg-color-primary;
    border-radius: 10px;
    outline: none;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &__label {
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translateY(-50%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    font-family: $font-family-primary;
    font-size: 14px;
    line-height: 140%;
    color: $text-color-primary;
  }

  &__textarea {
    display: block;
    margin-bottom: 24px;
    resize: none;

    @media screen and (min-width: $tablet-min) {
      margin-bottom: 32px;
    }
  }

  &__input:hover + &__label,
  &__input:focus + &__label,
  &__input:not(:placeholder-shown) + &__label {
    transform: translate(-10px, -40px) scale(0.7);
    font-weight: 500;
  }

  &__input:not(:placeholder-shown):required:valid {
    border: 1px solid $bg-color-primary;
  }

  &__input:not(:placeholder-shown):required:invalid {
    border: 1px solid red;
  }

  &__input:not(:placeholder-shown):required:invalid + &__label {
    color: red;
  }

  &__button {
    @extend .button;
    display: block;
    margin: 0 auto;
    padding: 12px 34px;
  }
}
