.form {
  width: 240px;
  margin: 0 auto;

  @media screen and (min-width: $tablet-min) {
    width: 368px;
  }
}

.form__input,
.form__textarea {
  width: 100%;
  padding: 11px 16px;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid $bg-color-primary;
  border-radius: 10px;
  outline: none;
}

.form__field {
  position: relative;
  margin-bottom: 16px;
}

.form__comment {
  position: relative;
  margin-bottom: 24px;

  @media screen and (min-width: $tablet-min) {
    margin-bottom: 32px;
  }
}

.form__textarea {
  resize: none;
  display: block;
}

.form__label {
  position: absolute;
  left: 16px;
  top: 11px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  font-family: $font-family-primary;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0;

  color: $text-color-primary;
}

.form__field:hover .form__label,
.form__field:focus .form__label,
.form__field:focus-within .form__label,
.form__comment:hover .form__label,
.form__comment:focus .form__label,
.form__comment:focus-within .form__label {
  transform: translate(-16px, -26px) scale(0.7);
}

.form__input:not(:placeholder-shown):required:valid {
  border: 1px solid $bg-color-primary;
}

.form__input:not(:placeholder-shown):required:invalid {
  border: 1px solid red;
}

.form__input:not(:placeholder-shown)+.form__label,
.form__textarea:not(:placeholder-shown)+.form__label {
  transform: translate(-16px, -26px) scale(0.7);
}

.form__button {
  display: block;
  margin: 0 auto;
  padding: 12px 34px;
  line-height: 1.21;
  text-align: center;
}

.form input::placeholder,
.form textarea::placeholder {
  color: rgba(248, 210, 78, 0.1);
}